<template>
    <div>
        <p v-for="(item, index) in list" :key="index" style="margin-bottom: 0;line-height: 14px"> {{ item.home }} | <span style="color:red">{{ item.list }} | {{ index }}</span> </p>
    </div>
</template>

<script>
export default {
    data() {
        return {
            time: null,
            list:[
                { list: 50, home:'list1' },
                { list: 30, home:'list1' },
                { list: 61, home:'list1' },
                { list: 52, home:'list1' },
                { list: 19, home:'list1' },
                { list: 24, home:'list1' },
                { list: 32, home:'list1' },
                { list: 84, home:'list1' },
                { list: 20, home:'list1' },
                { list: 90, home:'list1' },
                { list: 14, home:'list1' },
                { list: 26, home:'list1' },
                { list: 18, home:'list1' },
                { list: 45, home:'list1' },
                { list: 65, home:'list1' },
                { list: 71, home:'list1' },
                { list: 22, home:'list1' },
                { list: 45, home:'list1' },
                { list: 98, home:'list1' },
                { list: 56, home:'list1' },
                { list: 27, home:'list1' },
                { list: 31, home:'list1' },
                { list: 36, home:'list1' },
                { list: 28, home:'list1' },
                { list: 19, home:'list1' },
                { list: 33, home:'list1' },
                { list: 44, home:'list1' },
                { list: 55, home:'list1' },
                { list: 66, home:'list1' },
                { list: 44, home:'list1' },
                { list: 19, home:'list1' },
                { list: 88, home:'list1' }
            ]
        }
    },
    mounted() {
        this.info();
    },
    methods: {
        info(){
          this.time = setInterval(()=>{
               this.list.forEach(item=>{
                   if( item.list == 0 ){ clearInterval( this.time ); this.random(); return; }
                   item.list = item.list - 1;
               })
               console.log( this.time )
           },1000) 
        },
        random(){
            this.list.forEach(item=>{
                let random = parseInt(10 + (90 - 10) * (Math.random()))
                item.list = random;
            })
            this.info();
        }
    },
    destroyed: function () {
        if( this.time == null ){ return false };
        clearInterval( this.time );
    }
}
</script>