      <hr>
      <div style="width:320px;margin:0 auto" id="app">
        <DigitRoll ref="digitroll" :rollDigits="digits" :dur=1000 :flipStra="true" />
        <input type="text" v-model="input" />
        <button @click="changeDigit">click me</button>
      </div>
    </div>
  </div>
</template>

<script>
import DigitRoll from "@huoyu/vue-digitroll";
export default {
  components: { DigitRoll },
  data() {
    return {
      digits: 123456,
      input:'789456123'
    };
  },
  methods: {
    changeDigit() {
      this.$refs.digitroll.setDigit([
        {
          value: 2,
          dur: 600
        },
        {
          value: 4,
          dur: 900
        },
        {
          value: 6,
          dur: 1200
        },
        {
          value: 8,
          dur: 1500
        }
      ]);
    },
    // changeDigit() {
    //   this.$refs.digitroll.setDigit(this.input);
    // },
    // restart() {
    //   const random = `${Math.random()}`.substr(2).substr(0, 4);
    //   setTimeout(() => {
    //     this.digits = random;
    //   }, 1000);
    // }
  }
};
</script>